<template>
  <div class="mmm">
    <van-nav-bar title="时效问题" fixed left-arrow @click-left="$toPage(-1)">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="main">
      <img @click="onChange" src="..\..\assets\timer.png" />
      <van-image-preview
        v-model="show"
        :images="images"
        @change="onChange"
      />
    </div>
  </div>
</template>
<script>
import img from '../../assets/timer.png'
export default {
  data() {  
    return {
      images: [img],
      show: false
    };
  },
  methods: {
    onChange() {
      this.show = !this.show
    },
  }
};
</script>
<style scoped>
.main {
  width: 100%;
  padding: 92px 20px;
  box-sizing: border-box;
}
.main img {
  width: 100%;
}
</style>